import m from "mithril";
import { QueryListForm, QueryListFormAttrs } from "mithril-material-forms";
import { JSONPointer } from "../../types";
import Controller from "../../Controller";
import { Editor, Options as EditorOptions } from "../Editor";
import AbstractEditor from "../AbstractEditor";

export type EditronSchemaOptions = {
    /** theme option, passed to value-editors */
    theme?: string;
}

export type Options = EditorOptions & EditronSchemaOptions;

export default class AutocompleteEditor extends AbstractEditor {
    options: Options;
    childEditors: Array<Editor> = [];
    $children: HTMLElement;
    $headerChildren: HTMLElement;
    childOptions: { theme?: string };

    static editorOf(pointer: JSONPointer, controller: Controller) {
        const schema = controller.service("schema").get(pointer);
        return schema.format === "autocomplete" && schema.type === "string";
    }

    constructor(pointer: JSONPointer, controller: Controller, options: Options) {
        super(pointer, controller, options);


        this.render();
    }

    update() {
        console.log("Update was executed");
    }

    render() {
        m.render(this.dom, m(QueryListForm));
    }

    destroy() {
        console.log('DESTROY WAS EXECTUED');
    }
}
